import test from 'ava';
import { getApi, auth } from '../requests';

test.only('foo', async t => {
    const api = await getApi();
    await auth(api);
    const resp = await api.get('/groups');
    t.is(resp.status, 200);
    t.is(resp.data[0].feeds[0].name, 'new_feed');
});

